"use client"

import Link from "next/link"
import Image from "next/image"
import Header from "../components/Header"
import { ArrowLeft, ExternalLink } from "lucide-react"

export default function Portfolio() {
  const projects = [
    {
      title: "Portfolio design",
      description: "UI design - User research - webflow develop",
      image: "/assets/test.png",
      link: "#",
    },
    {
      title: "Portfolio design",
      description: "UI design - User research - webflow develop",
      image: "/assets/project2.jpg",
      link: "#",
    },
    {
      title: "I design & top",
      description: "UI design - User research - webflow develop",
      image: "/assets/project3.jpg",
      link: "#",
    },
    {
      title: "Find Your True Style",
      description: "UI design - User research - webflow develop",
      image: "/assets/project4.jpg",
      link: "#",
    },
  ]

  return (
    <main className="min-h-screen bg-background">
      <Header />

      {/* Content */}
      <section className="max-w-6xl mx-auto px-4 py-8 md:py-16">
        <Link href="/" className="inline-flex items-center gap-2 text-foreground font-bold mb-8 hover:text-accent transition">
          <ArrowLeft className="w-5 h-5" />
          Back to Home
        </Link>

        <div className="space-y-8 md:space-y-12">
          {/* Header */}
          <div className="flex items-center gap-3 md:gap-4">
            <h1 className="text-3xl md:text-5xl lg:text-6xl font-black">My Portfolio</h1>
            <Image
              src="/assets/el2.png"
              alt="Decoration"
              width={60}
              height={60}
              className="object-contain star-decoration w-12 h-12 md:w-16 md:h-16 lg:w-20 lg:h-20"
            />
          </div>

          {/* Projects Grid */}
          <div className="grid grid-cols-1 md:grid-cols-2 gap-6 md:gap-8 lg:gap-12">
            {projects.map((project, index) => (
              <Link
                key={index}
                href={project.link}
                className="group block relative"
              >
                {/* Container dengan aspect ratio */}
                <div className="relative w-full aspect-[4/3]">
                  {/* Background Frame - Paling belakang */}
                  <div className="absolute inset-0">
                    <Image
                      src="/assets/portoframe.png"
                      alt="Frame"
                      fill
                      className="object-contain"
                      priority={index < 2}
                    />
                  </div>

                  {/* Content Layer - Di tengah (di atas background) */}
                  <div className="absolute inset-0 p-[15%] flex flex-col justify-between">
                    {/* Image */}
                    <div className="relative flex-1 overflow-hidden rounded-sm">
                      <Image
                        src={project.image}
                        alt={project.title}
                        fill
                        className="object-cover"
                      />
                    </div>
                    
                    {/* Info di bawah image */}
                    <div className="mt-2 space-y-1 bg-background/95 backdrop-blur-sm p-2 rounded-sm border-2 border-foreground">
                      <div className="flex items-start justify-between gap-2">
                        <h3 className="text-sm md:text-base font-bold group-hover:text-accent transition line-clamp-1">
                          {project.title}
                        </h3>
                        <ExternalLink className="w-3 h-3 md:w-4 md:h-4 flex-shrink-0 group-hover:text-accent transition" />
                      </div>
                      <p className="text-[10px] md:text-xs text-muted-foreground line-clamp-1">
                        {project.description}
                      </p>
                    </div>
                  </div>
                </div>
              </Link>
            ))}
          </div>
        </div>
      </section>

      {/* Floating decorations - hide di mobile */}
      <div className="hidden md:block fixed top-32 right-12 text-4xl star-decoration opacity-70">âœ¨</div>
      <div className="hidden md:block fixed bottom-20 left-12 text-3xl star-decoration opacity-70">ðŸ’«</div>
    </main>
  )
}